import { SetStateAction, useEffect, useState } from 'react';
import './App.css';
import axios from 'axios';
import { Company } from './types/Company'
import CompanyDropdown from './components/CompanyDropdown'
import { CompanyData } from './types/CompanyData';
import CompanyChart from './components/CompanyChart';
import Spinner from 'react-bootstrap/Spinner';


function App() {
  const [companies, setCompanies] = useState<Company[] | null>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [selectedCompanyData, setSelectedCompanyData] = useState<CompanyData[] | null>()
  const selectedCompanyHandler = (company: Company) => {
    setLoading(true);
    const url = `https://data.nasdaq.com/api/v3/datasets/WIKI/${company.dataset_code}?api_key=FVby2pSTqW1_iT5wC5QM`
    axios.get(url, {
      headers: {
        Accept: 'application/json',
      },
    }).then((response: { data: { dataset: { data: { map: (arg0: (el: number[]) => { date: number; close: number; }) => SetStateAction<CompanyData[] | null | undefined>; }; }; }; }) => {
      setSelectedCompanyData(response.data.dataset.data.map((el: number[]) => {
        return {
          "date": el[0],
          "close": el[4]
        }
      }))
      setLoading(false);
    })
  }
  useEffect(() => {
    //on mount
    const url = 'https://data.nasdaq.com/api/v3/datasets/?database_code=WIKI&api_key=FVby2pSTqW1_iT5wC5QM'
    axios.get(url, {
      headers: {
        Accept: 'application/json',
      },
    }).then((response: { data: { datasets: SetStateAction<Company[] | null>; }; }) => {
      setCompanies(response.data.datasets)
    })
  }, [])

  return (
    <div>
      <header>
        <h2>Company dropdown</h2>
        {companies ? <CompanyDropdown companies={companies} selectedCompanyHandler={selectedCompanyHandler}></CompanyDropdown> : <p>Cannot load information</p>}
      </header>
      <div className='App-chart-container'>
        {!loading ? selectedCompanyData ? <CompanyChart companyData={selectedCompanyData}></CompanyChart> : null : <Spinner animation="border" role="status"><span className="visually-hidden">Loading...</span></Spinner>}
      </div>
    </div>
  );
}

export default App;
